.transactionHistory {
  width: 400px;
  margin: 20px auto;
  padding: 0;
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.42);
  border-radius: 5px;
}

.transactionHistory:hover,
.transactionHistory:focus {
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.61);
}
.tableHead {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: 'tableHeader tableHeader tableHeader';
  background-color: rgb(47, 192, 192);
  color: #ffffff;
}

.tableHeader {
  text-align: left;
  padding: 10px;
  border: 1px solid #ffffff;
}

.tableBody {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: 'type amount currency';
}

.even {
  composes: tableBody;
  background-color: rgb(252, 244, 244);
}
.odd {
  composes: tableBody;
  background-color: rgb(236, 236, 236);
}

.type {
  text-align: left;
  padding: 10px;
  border: 1px solid #ffffff;
}
.type:hover,
.type:focus {
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.32);
  border-radius: 5px;
  background-color: rgb(69, 56, 255);
  color: #ffffff;
}
.amount {
  composes: type;
}
.amount:hover,
.amount:focus {
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.42);
  border-radius: 5px;
  background-color: rgb(69, 56, 255);
  color: #ffffff;
}
.currency {
  composes: type;
}
.currency:hover,
.currency:focus {
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.42);
  border-radius: 5px;
  background-color: rgb(69, 56, 255);
  color: #ffffff;
}
